import React, { Component } from 'react';
import { TableData, ColEditDelete } from 'components/common';
import { getStudentSubject } from 'services/TeacherSubject';
import routeConstant from 'routes/route/routeConstant';

export default class TableTeacherSubject extends Component {
  state = {};

  columns = [
    {
      title: 'Mã giảng viên',
      dataIndex: 'teacherId',
      key: 'teacherId',
    },
    {
      title: 'Giảng viên',
      dataIndex: 'teacher',
      key: 'teacher',
    },
    {
      title: 'Môn học',
      dataIndex: 'subjects',
      key: 'subjects',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          linkEdit={{
            to: {
              pathname: routeConstant.DANH_MUC__CHUONG_TRINH_DAO_TAO__GIAO_VIEN_MON_HOC__CHI_TIET,
              search: `?teacherId=${record.teacherId}&teacher=${record.teacher}&subjects=${record.subjects}`,
            },
          }}
          hidenBtnDelete
        />
      ),
    },
  ];

  render() {
    const { paramSearch } = this.props;

    return (
      <TableData
        formSearch
        service={getStudentSubject}
        paramSearch={paramSearch}
        columns={this.columns}
      />
    );
  }
}



