import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';

import { getQueryParams } from 'utils/urlUtil';
import routeConstant from 'routes/route/routeConstant';
import { BtnAddNew } from 'components/common/button';

import FormSearch from './FormSearch';
import TableTeacherSubject from './TableTeacherSubject';

class ManualSchedulePage extends Component {
  constructor(props) {
    super(props);
    this.refDataSheet = React.createRef();
    this.selectedCells = {};
    this.planDetailDataToSave = {};
    this.planDetailDataToDelete = [];
    this.state = {
      paramSearch: {},
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    this.setState({
      paramSearch: getQueryParams(),
    });
  };

  linkAddNew = {
    to: {
      pathname: routeConstant.DANH_MUC__CHUONG_TRINH_DAO_TAO__GIAO_VIEN_MON_HOC__CHI_TIET,
    },
  };

  render() {
    const {
      paramSearch,
    } = this.state;

    return (
      <Card title="Giảng viên - Môn học">
        <Row gutter={24}>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <Link {...this.linkAddNew}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <TableTeacherSubject
              paramSearch={paramSearch}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManualSchedulePage />
  </Page>
);



